import 'bootstrap';
import 'bootstrap-select';
import makeSwiper from './modules/swiper';
import './modules/sign';
import './modules/tabs';
import './modules/brick';
import './modules/product-slider';
import api from './modules/api';
import MyDate from './modules/myDate';

$(() => {
  makeSwiper('.banner-slider');
  $('.btn-collection').click(function () {
    if ($(this).hasClass('logined')) {
      var type = $(this).data('type')
      var goods_id = $(this).data('id')
      if ($(this).hasClass('active')) {
        $(this).find('label').text('收藏')
      } else {
        $(this).find('label').text('已收藏')
      }

      $(this).toggleClass('active')
      api.usercenter.toggleFav(type, goods_id);
    }
  })
  var d = new MyDate()

  var winHeight = $(document).scrollTop();
  $(window).scroll(function () {
    var scrollY = $(document).scrollTop(); // 获取垂直滚动的距离，即滚动了多少
    if (scrollY > 80) { //如果滚动距离大于550px则隐藏，否则删除隐藏类
      $('.c-header').addClass('hiddened');
      $('.search-box').addClass('p-fixed');
    } else {
      $('.c-header').removeClass('hiddened');
      $('.search-box').removeClass('p-fixed');
    }
    if (scrollY > winHeight) { //如果没滚动到顶部，删除显示类，否则添加显示类
      $('.c-header').removeClass('showed');
    } else {
      $('.c-header').addClass('showed');
    }
  });
});

window.Apitool = api



